

import { StyleSet, Theme } from "@here/harp-datasource-protocol";
import { GeoCoordinates } from "@here/harp-geoutils";
import { GeoJsonDataProvider, OmvDataSource } from "@here/harp-omv-datasource";
import {
    FeaturesDataSource,
    MapViewFeature,
    MapViewLineFeature,
    MapViewMultiPointFeature
} from "@here/harp-features-datasource";
import { View } from "./View";

const harp = new View({
    canvas: document.getElementById("map")
});

const mapView = harp.mapView;

// make map full-screen
mapView.resize(window.innerWidth, window.innerHeight);

// react on resize events from the browser.
window.addEventListener("resize", () => {
    mapView.resize(window.innerWidth, window.innerHeight);
});

// center the camera to New York
const LDN = new GeoCoordinates(51.507, -0.127);
mapView.lookAt(LDN, 1500, 40, 0);

mapView.lookAt({ tilt: 85 });
mapView.lookAt({ heading: -20 });





// make sure the map is rendered
mapView.update();



const featuresDataSource = new FeaturesDataSource({ styleSetName: "geojson" });
    mapView.addDataSource(featuresDataSource);

fetch('https://sjmarshalluk.github.io/london_path.geojson')
.then(data => data.json())
.then(data => {
   const geoJsonDataProvider = new GeoJsonDataProvider("https://sjmarshalluk.github.io/london_path", data);
   const geoJsonDataSource = new OmvDataSource({
      dataProvider: geoJsonDataProvider,
      name: "https://sjmarshalluk.github.io/london_path"
   });

    mapView.addDataSource(geoJsonDataSource).then(() => {
      
        const styles = [{
         "when": "$geometryType ^= 'line'",
         "renderOrder": 1000,
         "technique": "solid-line",
         "attr": {
            "color": "black",
            "opacity": 1,
            "metricUnit": "Pixel",
            "lineWidth": 10
         }
      }];
       geoJsonDataSource.setStyleSet(styles);
       
       mapView.update();
       console.log(data);
    });
});



function handleOrientation(event) {
    mapView.lookAt({ tilt: event.beta });

    //var rotation = event.gamma - (event.gamma * 2);
    mapView.lookAt({ heading: event.gamma });


}



let is_running = false;
let demo_button = document.getElementById("start_demo");
demo_button.onclick = function(e) {
  e.preventDefault();

  if (
    DeviceMotionEvent &&
    typeof DeviceMotionEvent.requestPermission === "function"
  ) {
    DeviceMotionEvent.requestPermission();
  }
  
  if (is_running){
    //window.removeEventListener("devicemotion", handleMotion);
    window.removeEventListener("deviceorientation", handleOrientation);
    demo_button.innerHTML = "Start demo";
    demo_button.classList.add('btn-success');
    demo_button.classList.remove('btn-danger');
    is_running = false;
  }else{
    //window.addEventListener("devicemotion", handleMotion);
    window.addEventListener("deviceorientation", handleOrientation);
    document.getElementById("start_demo").innerHTML = "Stop demo";
    demo_button.classList.remove('btn-success');
    demo_button.classList.add('btn-danger');
    is_running = true;
  }
};

